import React, { useEffect, useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { CheckCircle, Circle, Clock, AlertTriangle } from 'lucide-react';

interface Task {
    id: number;
    title: string;
    description: string;
    assigned_to: string;
    status: string;
    priority: string | number;
    created_at: string;
}

interface BacklogState {
    tasks: Task[];
}

const PriorityBadge = ({ priority }: { priority: string | number }) => {
    let color = 'bg-blue-500/20 text-blue-400';
    let label = strPriority(priority);

    if (label === 'critical' || priority === 1) color = 'bg-red-500/20 text-red-400';
    else if (label === 'high' || priority === 2) color = 'bg-orange-500/20 text-orange-400';
    else if (label === 'medium' || priority === 3) color = 'bg-yellow-500/20 text-yellow-400';

    return (
        <span className={`px-2 py-0.5 rounded text-[10px] uppercase font-bold tracking-wider ${color}`}>
            {label}
        </span>
    );
};

const strPriority = (p: string | number) => {
    if (typeof p === 'number') {
        if (p === 1) return 'critical';
        if (p === 2) return 'high';
        if (p === 3) return 'medium';
        return 'low';
    }
    return p;
};

const TaskCard = ({ task }: { task: Task }) => (
    <motion.div
        layout
        initial={{ opacity: 0, scale: 0.95 }}
        animate={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0, scale: 0.95 }}
        className="bg-white/5 border border-white/10 p-3 rounded-lg mb-2 hover:border-white/20 transition-colors group"
    >
        <div className="flex justify-between items-start mb-2">
            <PriorityBadge priority={task.priority} />
            <span className="text-[10px] text-white/30 font-mono">#{task.id}</span>
        </div>
        <h4 className="text-sm font-medium text-white mb-1 group-hover:text-primary transition-colors">
            {task.title}
        </h4>
        <p className="text-xs text-white/50 line-clamp-2 mb-2">
            {task.description}
        </p>
        <div className="flex justify-between items-center mt-2 pt-2 border-t border-white/5">
            <div className="flex items-center gap-1 text-[10px] text-white/40">
                <div className="w-4 h-4 rounded-full bg-white/10 flex items-center justify-center text-[8px]">
                    {task.assigned_to?.[0]?.toUpperCase() || '?'}
                </div>
                {task.assigned_to}
            </div>
            <div className="text-[10px] text-white/30">
                {new Date(task.created_at).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
            </div>
        </div>
    </motion.div>
);

export default function TaskBoard() {
    const [tasks, setTasks] = useState<Task[]>([]);

    useEffect(() => {
        const fetchTasks = async () => {
            try {
                const res = await fetch('http://localhost:5000/api/backlog');
                const data = await res.json();
                setTasks(data.tasks || []);
            } catch (err) {
                console.error(err);
            }
        };
        fetchTasks();
        const interval = setInterval(fetchTasks, 3000);
        return () => clearInterval(interval);
    }, []);

    const todo = tasks.filter(t => t.status === 'todo');
    const inProgress = tasks.filter(t => t.status === 'in_progress');
    const done = tasks.filter(t => t.status === 'done').slice(-5); // Show last 5 done

    return (
        <div className="h-full flex flex-col">
            <h2 className="text-lg font-bold text-white tracking-wide mb-4 flex items-center gap-2">
                <CheckCircle className="w-5 h-5 text-primary" />
                Task Board
            </h2>

            <div className="grid grid-cols-3 gap-4 h-full min-h-0">
                {/* To Do */}
                <div className="bg-white/5 rounded-xl p-3 flex flex-col min-h-0">
                    <div className="flex items-center justify-between mb-3 pb-2 border-b border-white/5">
                        <span className="text-xs font-bold text-white/60 uppercase tracking-wider">To Do</span>
                        <span className="bg-white/10 px-1.5 py-0.5 rounded text-xs text-white/60">{todo.length}</span>
                    </div>
                    <div className="overflow-y-auto pr-2 custom-scrollbar flex-1 min-h-0">
                        <AnimatePresence>
                            {todo.map(t => <TaskCard key={t.id} task={t} />)}
                        </AnimatePresence>
                    </div>
                </div>

                {/* In Progress */}
                <div className="bg-white/5 rounded-xl p-3 flex flex-col min-h-0">
                    <div className="flex items-center justify-between mb-3 pb-2 border-b border-white/5">
                        <span className="text-xs font-bold text-blue-400 uppercase tracking-wider">In Progress</span>
                        <span className="bg-blue-500/20 px-1.5 py-0.5 rounded text-xs text-blue-400">{inProgress.length}</span>
                    </div>
                    <div className="overflow-y-auto pr-2 custom-scrollbar flex-1 min-h-0">
                        <AnimatePresence>
                            {inProgress.map(t => <TaskCard key={t.id} task={t} />)}
                        </AnimatePresence>
                    </div>
                </div>

                {/* Done */}
                <div className="bg-white/5 rounded-xl p-3 flex flex-col min-h-0">
                    <div className="flex items-center justify-between mb-3 pb-2 border-b border-white/5">
                        <span className="text-xs font-bold text-green-400 uppercase tracking-wider">Done</span>
                        <span className="bg-green-500/20 px-1.5 py-0.5 rounded text-xs text-green-400">{done.length}</span>
                    </div>
                    <div className="overflow-y-auto pr-2 custom-scrollbar flex-1 min-h-0">
                        <AnimatePresence>
                            {done.map(t => <TaskCard key={t.id} task={t} />)}
                        </AnimatePresence>
                    </div>
                </div>
            </div>
        </div>
    );
}
