import React, { useEffect, useState } from 'react';
import { motion } from 'framer-motion';
import { Brain, Activity, AlertCircle, CheckCircle, Clock } from 'lucide-react';

interface AgentState {
    status: 'working' | 'idle' | 'error' | 'stopped';
    current_task: string | null;
    task_start: number | null;
    tasks_completed: number;
    tasks_failed: number;
    token_usage: number;
    last_activity: string;
}

const AgentCard = ({ name, data }: { name: string; data: AgentState }) => {
    const isWorking = data.status === 'working';
    const isError = data.status === 'error';

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className={`p-4 rounded-xl border ${isWorking ? 'border-primary/50 bg-primary/5' :
                    isError ? 'border-red-500/50 bg-red-500/5' :
                        'border-white/10 bg-white/5'
                } backdrop-blur-md relative overflow-hidden group`}
        >
            <div className="flex justify-between items-start mb-3">
                <div className="flex items-center gap-3">
                    <div className={`p-2 rounded-lg ${isWorking ? 'bg-primary/20 text-primary' :
                            isError ? 'bg-red-500/20 text-red-500' :
                                'bg-white/10 text-white/60'
                        }`}>
                        <Brain className="w-5 h-5" />
                    </div>
                    <div>
                        <h3 className="font-bold text-white tracking-wide">{name.replace('_', ' ')}</h3>
                        <span className={`text-xs uppercase tracking-wider font-semibold ${isWorking ? 'text-primary' : isError ? 'text-red-400' : 'text-white/40'
                            }`}>
                            {data.status}
                        </span>
                    </div>
                </div>
                {isWorking && (
                    <motion.div
                        animate={{ rotate: 360 }}
                        transition={{ duration: 2, repeat: Infinity, ease: 'linear' }}
                    >
                        <Activity className="w-5 h-5 text-primary" />
                    </motion.div>
                )}
            </div>

            <div className="space-y-2">
                <div className="flex items-center gap-2 text-sm text-white/60">
                    <Clock className="w-4 h-4" />
                    <span className="truncate max-w-[200px]">
                        {data.current_task || 'Waiting for tasks...'}
                    </span>
                </div>

                <div className="grid grid-cols-3 gap-2 mt-4 pt-4 border-t border-white/5">
                    <div className="text-center">
                        <div className="text-xs text-white/40 mb-1">Done</div>
                        <div className="text-sm font-mono text-green-400">{data.tasks_completed}</div>
                    </div>
                    <div className="text-center">
                        <div className="text-xs text-white/40 mb-1">Failed</div>
                        <div className="text-sm font-mono text-red-400">{data.tasks_failed}</div>
                    </div>
                    <div className="text-center">
                        <div className="text-xs text-white/40 mb-1">Tokens</div>
                        <div className="text-sm font-mono text-blue-400">
                            {(data.token_usage / 1000).toFixed(1)}k
                        </div>
                    </div>
                </div>
            </div>

            {isWorking && (
                <div className="absolute inset-x-0 bottom-0 h-1 bg-gradient-to-r from-transparent via-primary to-transparent opacity-50 animate-pulse" />
            )}
        </motion.div>
    );
};

export default function AgentStatus() {
    const [agents, setAgents] = useState<Record<string, AgentState>>({});

    useEffect(() => {
        const fetchStatus = async () => {
            try {
                const res = await fetch('http://localhost:5000/api/status');
                const data = await res.json();
                setAgents(data);
            } catch (err) {
                console.error('Failed to fetch agent status:', err);
            }
        };

        fetchStatus();
        const interval = setInterval(fetchStatus, 2000);
        return () => clearInterval(interval);
    }, []);

    return (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-5 gap-4">
            {Object.entries(agents).map(([name, data]) => (
                <AgentCard key={name} name={name} data={data} />
            ))}
        </div>
    );
}
