import React, { useEffect, useState } from 'react';
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import { TrendingUp, TrendingDown, Activity } from 'lucide-react';

interface Position {
    symbol: string;
    qty: number;
    entry_price: number;
    entry_time: string;
}

interface PortfolioState {
    capital: number;
    positions: Record<string, Position>;
    closed_trades: any[];
    equity_curve: { timestamp: number; equity: number }[];
    total_pnl: number;
}

const StatCard = ({ label, value, trend }: { label: string; value: string; trend?: number }) => (
    <div className="bg-white/5 border border-white/10 rounded-xl p-4 backdrop-blur-md">
        <div className="text-white/40 text-sm mb-1">{label}</div>
        <div className="text-2xl font-bold text-white tracking-tight">{value}</div>
        {trend !== undefined && (
            <div className={`text-xs mt-2 flex items-center gap-1 ${trend >= 0 ? 'text-green-400' : 'text-red-400'}`}>
                {trend >= 0 ? <TrendingUp className="w-3 h-3" /> : <TrendingDown className="w-3 h-3" />}
                {trend > 0 ? '+' : ''}{trend.toFixed(1)}%
            </div>
        )}
    </div>
);

export default function TradingOverview() {
    const [data, setData] = useState<PortfolioState | null>(null);

    useEffect(() => {
        const fetchPortfolio = async () => {
            try {
                const res = await fetch('http://localhost:5000/api/portfolio');
                const json = await res.json();
                setData(json);
            } catch (err) {
                console.error('Failed to fetch portfolio:', err);
            }
        };

        fetchPortfolio();
        const interval = setInterval(fetchPortfolio, 5000);
        return () => clearInterval(interval);
    }, []);

    if (!data) return <div className="text-white/40 animate-pulse">Loading market data...</div>;

    // Safe Accessors
    const capital = data.capital || 0;
    const history = data.equity_curve || [];

    // Calculate value safely
    // If we have history, use latest. If not, use capital (assuming initial state)
    const lastEquity = history.length > 0 ? history[history.length - 1].equity : capital;

    // Also sum positions cost if initial
    // But wait, position cost is subtracted from capital. 
    // So Capital = Cash. Equity = Cash + Position Value.
    // If no history, assume positions value ~ cost.
    const positionValue = Object.values(data.positions || {}).reduce((sum, p) => sum + (p.qty * p.entry_price), 0);
    const currentVal = history.length > 0 ? lastEquity : (capital + positionValue);

    const initialValue = 10000;
    const pnlPercent = ((currentVal - initialValue) / initialValue) * 100;

    return (
        <div className="h-full flex flex-col gap-4">
            <div className="flex items-center gap-2 mb-2">
                <Activity className="w-5 h-5 text-primary" />
                <h2 className="text-lg font-bold text-white tracking-wide">Live Trading</h2>
            </div>

            <div className="grid grid-cols-2 gap-4">
                <StatCard label="Portfolio Value" value={`$${currentVal.toLocaleString()}`} trend={pnlPercent} />
                <StatCard label="Cash Balance" value={`$${capital.toLocaleString()}`} />
            </div>

            <div className="bg-white/5 border border-white/10 rounded-xl p-4 backdrop-blur-md flex-1 min-h-[200px]">
                <h3 className="text-white/60 text-sm mb-4">Equity Curve</h3>
                <ResponsiveContainer width="100%" height="100%">
                    <AreaChart data={history}>
                        <defs>
                            <linearGradient id="colorValue" x1="0" y1="0" x2="0" y2="1">
                                <stop offset="5%" stopColor="#3b82f6" stopOpacity={0.3} />
                                <stop offset="95%" stopColor="#3b82f6" stopOpacity={0} />
                            </linearGradient>
                        </defs>
                        <CartesianGrid strokeDasharray="3 3" stroke="#ffffff10" vertical={false} />
                        <XAxis
                            dataKey="timestamp"
                            tickFormatter={(ts) => new Date(ts * 1000).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                            stroke="#ffffff40"
                            tick={{ fontSize: 10 }}
                            tickLine={false}
                            axisLine={false}
                        />
                        <YAxis
                            domain={['auto', 'auto']}
                            stroke="#ffffff40"
                            tick={{ fontSize: 10 }}
                            tickFormatter={(val) => `$${(val / 1000).toFixed(0)}k`}
                            tickLine={false}
                            axisLine={false}
                        />
                        <Tooltip
                            contentStyle={{ backgroundColor: '#171717', border: '1px solid #333', color: '#fff' }}
                            labelFormatter={(ts) => new Date(ts * 1000).toLocaleString()}
                            formatter={(val: any) => [`$${Number(val).toLocaleString()}`, 'Value']}
                        />
                        <Area
                            type="monotone"
                            dataKey="equity"
                            stroke="#3b82f6"
                            strokeWidth={2}
                            fillOpacity={1}
                            fill="url(#colorValue)"
                        />
                    </AreaChart>
                </ResponsiveContainer>
            </div>
        </div>
    );
}
